import { Component, OnInit } from '@angular/core';
import { NgForm , ValidationErrors, FormGroup, Validators, FormControl} from "@angular/forms";
import { UserService } from "../../shared/user.service";
import { Router } from '@angular/router';

@Component({
  selector: 'app-empdata',
  templateUrl: './empdata.component.html',
  styleUrls: ['./empdata.component.css']
})
export class EmpdataComponent implements OnInit {
  emailRegex = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  showSucessMessage: boolean;
  serverErrorMessages: string;
  // router: any;

  constructor(private userService: UserService,private router: Router) { }

  ngOnInit() {
  }

onSubmit(form : NgForm){
  this.userService.postEmp(form.value).subscribe(
    res => {
      this.showSucessMessage =true;
      setTimeout(() => this.showSucessMessage= false,4000);
      this.resetForm(form);
    },
    err => {
      if(err.status == 422){
        this.serverErrorMessages = err.error.join('<br/>');
      }
      else
        this.serverErrorMessages = 'Something went Wrong, Please contact Sra'
    } 
  );
}

resetForm(form: NgForm) {
  this.userService.selectedEmp = {
    empid:'',
    empname:'',
    empteam:'',
    empemail:'',
    password:''
  };
form.resetForm();
this.serverErrorMessages = '';
  }

  testForm:FormGroup = new FormGroup({
    empid:new FormControl(null,[Validators.required]),
    empname:new FormControl(null,[Validators.required]),
    empteam:new FormControl(null,[Validators.required]),
    empemail:new FormControl(null,[Validators.required]),
   

    //empname:new FormControl(null,[Validators.required]),
    //empteam:new FormControl(null,[Validators.required]),
    //password:new FormControl(null,[Validators.required]),
    //cpass:new FormControl(null,[Validators.required])
  })



  onReg(){
    
    var edteam = (document.getElementById('empteam') as HTMLInputElement).value;
    alert('selected team is '+edteam);
    this.testForm.get('empteam').setValue(edteam);
    if(!this.testForm.valid){
      // console.log(JSON.stringify(this.inOutForm.value))
      console.log('Invalid Form'); 
      alert('Please fill all the fields');
      
      return;
    }
    
    this.userService.postTest(this.testForm.value)
    .subscribe(
      // data=> {console.log(data); this._router.navigate(['/result']);},
      data=> console.log(data),
      error=>console.error(error),
      
    )
    this.router.navigate(['/login']);
}
}

